<template>
    <div>
        <v-navigation-drawer
            v-model="drawer"
            app
            temporary
            dark
        >
            <v-list>
                <v-list-item>
                    <v-list-item-content class="logotext-mobile">
                        <v-list-item-title class="maintext-mobile text-h6">
                            Headshot
                        </v-list-item-title>

                        <v-list-item-subtitle class="subtext-mobile">
                            barbershop
                        </v-list-item-subtitle>
                    </v-list-item-content>
                </v-list-item>
            </v-list>

            <v-divider />

            <v-list dense>
                <v-list-item
                    v-for="([icon, text, link], i) in items"
                    :key="i"
                    link
                    @click="$vuetify.goTo(link)"
                >
                        <v-list-item-icon class="justify-center">
                            <v-icon>{{ icon }}</v-icon>
                        </v-list-item-icon>

                        <v-list-item-content>
                            <v-list-item-title class="subtitile-1">
                                {{text}}
                            </v-list-item-title>
                        </v-list-item-content>
                </v-list-item>
            </v-list>
        </v-navigation-drawer>

        <v-app-bar
            app
            :color="color"
            :flat="flat"
            dark
            class="px-10"
            height="100px"
            :class="{ expand: flat }"
        >

            <v-toolbar-title class="overline">
                <h2 class="main-text">headshot</h2>
                <h3 class="submain-text">barbershop.</h3>
            </v-toolbar-title>


            <v-spacer />

            <v-app-bar-nav-icon
                @click.stop="drawer = !drawer"
                class="mr-4"
                v-if="isXs"
            />

            <div v-else>
                <v-btn text @click="$vuetify.goTo('#home')">
                    <span class="mr-2">Главная</span>
                </v-btn>

                <v-btn text @click="$vuetify.goTo('#about')">
                    <span class="mr-2">О нас</span>
                </v-btn>

                <v-btn text @click="$vuetify.goTo('#work')">
                    <span class="mr-2">Работы</span>
                </v-btn>

                <v-btn text @click="$vuetify.goTo('#services')">
                    <span class="mr-2">Услуги</span>
                </v-btn>

                <v-btn text @click="$vuetify.goTo('#coworkers')">
                    <span class="mr-2">Специалисты</span>
                </v-btn>
            </div>
        </v-app-bar>
    </div>
</template>

<style lang="scss" scoped>
    .logotext-mobile {
        text-transform: uppercase;

        .maintext-mobile {

        }

        .subtext-mobile {

        }
    }

    .v-toolbar {
    transition: 0.6s;
    }

    .v-toolbar__title {
        text-transform: uppercase;
        .main-text {
            color: rgba(255, 255, 255, 0.753);
        }
        .submain-text {
            color: rgba(255, 255, 255, 0.753);
            margin-top: -1.2em;
        }
    }

</style>

<script>
export default {
  data: () => ({

    drawer: null,
    isXs: false,
    items: [
      ["mdi-circle-outline", "Главная", "#home"],
      ["mdi-circle-slice-1", "О нас", "#about"],
      ["mdi-circle-slice-2", "Работы", "#work"],
      ["mdi-circle-slice-3", "Услуги", "#services"],
      ["mdi-circle-slice-4", "Специалисты", "#coworkers"],
    ],
  }),
  props: {
    color: String,
    flat: Boolean,
  },
  methods: {
    onResize() {
      this.isXs = window.innerWidth < 850;
    },
  },
  watch: {
    isXs(value) {
      if (!value) {
        if (this.drawer) {
          this.drawer = false;
        }
      }
    },
  },
  mounted() {
    this.onResize();
    window.addEventListener("resize", this.onResize, { passive: true });
  },
};
</script>