<template>
    <v-app>
        <Navigation :color="color" :flat="flat"/>

        <v-main class="pt-0">
            <HomeSection/>
            <WorkSection/>
            <Services/>
            <CoWorkers/>
            <Maps/>
        </v-main>

        <Footer/>

        <v-scale-transition>
            <v-btn
                fab
                v-show="fab"
                v-scroll="onScroll"
                dark
                fixed
                bottom
                right
                color="secondary"
                @click="toTop"
            >

                <v-icon>mdi-arrow-up</v-icon>
            </v-btn>
        </v-scale-transition>

    </v-app>

</template>

<script>
import Navigation from './components/Navigation.vue';
import HomeSection from './components/HomeSection.vue';
import WorkSection from './components/WorkSection.vue';
import Services from './components/Services.vue';
import CoWorkers from './components/CoWorkers.vue';
import Maps from './components/Maps.vue';
import Footer from './components/Footer.vue';

export default {
  name: 'App',

  components: {
    Navigation,
    HomeSection,
    WorkSection,
    Services,
    CoWorkers,
    Maps,
    Footer
  },

  data: () => ({
    fab: null,
    color: "",
    flat: null,
  }),

    created() {
    const top = window.pageYOffset || 0;
    if (top <= 60) {
      this.color = "transparent";
      this.flat = true;
    }
  },
  watch: {
    fab(value) {
      if (value) {
        this.color = "secondary";
        this.flat = false;
      } else {
        this.color = "transparent";
        this.flat = true;
      }
    },
  },
  methods: {
    onScroll(e) {
      if (typeof window === "undefined") return;
      const top = window.pageYOffset || e.target.scrollTop || 0;
      this.fab = top > 60;
    },
    toTop() {
      this.$vuetify.goTo(0);
    },
  },
};
</script>

<style scoped>
    .pretty-header {
        font-size: 17px;
        line-height: 17px;
        margin: 20px 0px;
        position: relative;
        overflow: hidden;
        letter-spacing: 5px;
    }

</style>